@import 'vars';

.root {
    width: 100%;
    height: 100%;
    z-index: 1000000;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #333;
    user-select: none;
    opacity: 0;
    overflow: hidden;
}

.closeBtn {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 10000000;
    position: absolute;
    top: 0;
    right: 0;

    > svg {
        display: block;
        transform-origin: 50% 50%;
        transition: transform 0.3s ease;
    }

    &:hover > svg {
        transform: rotate(-90deg);
    }
}

.content {
    width: 100%;
    height: 100%;
}

.swiper {
    width: 100%;
    height: 100%;
}

.slide {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.slideContent {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;

    &.active {
        .imageBox > img.loaded {
            transform: none;
            opacity: 1;
        }

        .annotation {
            transform: none;
            opacity: 1;
        }
    }
}

.imageBox {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    flex-grow: 1;
    flex-shrink: 1;
    padding: 16px 80px 30px 80px;
    overflow: hidden;
    position: relative;

    .closableOverlay {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }

    &.noPagination {
        padding: 16px;

        img {
            transform: scale(0.5);
            opacity: 0;

            &.loaded {
                animation: reveal 0.6s ease forwards;
            }
        }

        @media all and (max-width: $mobile-breakpoint) {
            padding: 60px 0 30px 0;
        }
    }

    > img {
        display: block;
        opacity: 0;
        position: relative;
        z-index: 1;
        margin: auto;
        width: auto;
        height: auto;
        max-width: 100%;
        max-height: 100%;
        border-radius: 10px;
        transform: scale(0.5);
        transition: transform 0.6s ease, box-shadow 0.6s ease, opacity 0.6s ease;
    }

    @media all and (max-width: $mobile-breakpoint) {
        padding: 60px 0 30px 0;

        img {
            border-radius: 0;
        }
    }
}

.annotation {
    width: 100%;
    flex-shrink: 0;
    font-weight: normal;
    font-size: 16px;
    color: #fff;
    padding: 0 16px 34px 16px;
    text-align: center;
    margin-top: -14px;
    transform: scale(0.8);
    opacity: 0;
    transition: transform 0.6s ease, opacity 0.6s ease;
    transition-delay: 0.3s;
}

.clickablePagination > span {
    background-color: #555;
    opacity: 1;
    cursor: pointer;
    transition: background 0.3s ease;

    &.activeBullet {
        background-color: #f5f5f5;
        opacity: 1;
    }

    &:not(.activeBullet):hover {
        background-color: #999;
    }
}

.navButton {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: calc(50% - 30px);
    background-color: rgba(0, 0, 0, 0.2);
    color: #fff;
    z-index: 1;
    cursor: pointer;
    transition: transform 0.3s ease;

    &.prev {
        left: 0;
        transform: translateX(-10px);
        border-radius: 0 30px 30px 0;

        > svg {
            display: block;
        }
    }

    &.next {
        right: 0;
        transform: translateX(10px);
        border-radius: 30px 0 0 30px;

        > svg {
            display: block;
        }
    }

    &:hover {
        transform: translateX(0);
    }

    @media all and (max-width: $mobile-breakpoint) {
        display: none;
    }
}

@keyframes reveal {
    0% {
        transform: scale(0.5);
        opacity: 0;
    }
    100% {
        transform: none;
        opacity: 1;
        box-shadow: 0 0 30px rgba(0, 0, 0, 0.6);
    }
}

.preloader {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
